*{
  box-sizing: border-box;
  padding: 0;
  margin: 0;
  font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
}

:root{
  --main-color : #BAFF39;
  --minor-color : #6E6E6E;
  --white: #FFFFFF

}

.App {
  background-color: var(--main-color) ;
  min-height: 100vh;
}

.App > h1 {
  padding: 10px;
  text-align: center;
}

.inputs {
  min-height: 100px;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 15px;
  background-color: var(--minor-color);
  width: 60%;
  margin: auto;
  padding: 0 10%;
}

.inputs input{
  border: none;
  padding: 5px;
  flex: 1;
  border-radius: 5px;
  font-weight: bold;
}

.inputs input:focus{
  outline: none;
}

.inputs button{
  border: none;
  padding: 5px 10px;
  cursor: pointer;
  transition: all 0.4s ease-in-out;
  font-weight: bold;
  border-radius: 5px;
}

.inputs button:hover{
  box-shadow: 0px 0px 10px 1px black;
}


.task-list{
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  margin-top: 25px;
}

.task-list > .wraper{
  padding: 20px 40px;
  background-color: var(--minor-color);
  width: 60%;
}

.task{
  background-color: white;
  padding: 10px 15px;
  display: flex;
  align-items: center;
  margin-bottom: 10px;
  border-radius: 5px;
}

.task span{
  flex: 1;
}

.task button , .modal button{
  background-color: black;
  color: white;
  padding: 4px 8px;
  cursor: pointer;
  transition: all 0.2s ease-in-out;
  border-radius: 5px;

}

.task button:hover , .modal button:hover{
  box-shadow: 0px 0px 2px 1px var(--minor-color);
}

.modal{
  display: flex;
  justify-content: center;
  align-items: center;
  position: fixed;
  width: 100vw;
  height: 100vh;
  background-color: rgba(0, 0, 0, 0.773);
  z-index: 100;
  top: 0;
}

.modal-content{
  background-color: rgb(218, 218, 218);
  padding: 15px 30px;
  text-align: center;
  box-shadow: 0px 0px 10px 5px rgba(255, 255, 255, 0.321);
  display: flex;
  flex-direction: column;
  gap: 20px;
}

.modal-head {
  padding: 5px;
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
}

.modal-head button{
  position: absolute;
  top: 0;
  right: 0;
}

.modal-actions{
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 20px;
}
